import { Component } from '@angular/core';
import { Platform, App, Events, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage } from '../pages/tabs/tabs';
import { LoginPage } from '../pages/login/login';
import { SignUpPage } from '../pages/sign-up/sign-up';

import { Storage } from '@ionic/storage';

import { AuthenticationProvider } from '../providers/authentication/authentication';

import {AppSettings} from './app.settings';

import { LocalNotifications } from '@ionic-native/local-notifications';

import { BackgroundMode } from '@ionic-native/background-mode';

import { Geolocation } from '@ionic-native/geolocation';

import { OneSignal } from '@ionic-native/onesignal';

declare var cordova: any;
declare var window: any;
declare var mqtt: any;
import * as mqttJs from 'mqtt';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = LoginPage;
  public useNative:boolean = false;
  public client:any;
  public connectionStatus:boolean = true;
  public backgroundTimeout:any;
  public user:any;
  

  constructor(public app: App, public platform: Platform, public geolocation: Geolocation, public statusBar: StatusBar, public splashScreen: SplashScreen, public authProvider: AuthenticationProvider, public storage: Storage, public events: Events, private backgroundMode: BackgroundMode, private localNotifications: LocalNotifications,private alertCtrl: AlertController, private oneSignal: OneSignal) {
    platform.ready().then(() => {
        // Okay, so the platform is ready and our plugins are available.
        // Here you can do any higher level native things you might need.
        //statusBar.styleDefault();
        if (this.platform.is("android")) {
            this.statusBar.backgroundColorByHexString("#952517");
            this.statusBar.styleLightContent();
        }
        else{
            this.statusBar.styleLightContent();
        }

        
        this.storage.get("user").then((token) => {
 
            if (token){
                this.user = token;
                this.app.getRootNav().setRoot(TabsPage);
                this.app.getRootNav().popToRoot();  
                this.savePushId();                     

            }
            setTimeout(() => {this.splashScreen.hide();},500);
        }).catch(() => {
            this.splashScreen.hide();
        });
        
        this.events.subscribe("user:loggedin", () => {
            this.storage.get("user").then((token) => {

                if (token){
                    this.user = token;    
                    this.savePushId();
                }
            });        
            
            
        });
        
        if (platform.is("cordova")){
            
            if (platform.is("android")){
                cordova.plugins.DozeOptimize.IsIgnoringBatteryOptimizations(function (response){

                    if(response=="false"){
                        setTimeout(() => {
                            cordova.plugins.DozeOptimize.RequestOptimizations(function (response){

                            }, function (error){			
                            });
                        },3000);
                    }
                    else{
                    }		
                    }, function (error){   
                    }); 
                
            }   
                

            this.oneSignal.startInit("81102899-2cf7-491c-b936-5c5c300269d2", "420587777749");
            
            this.oneSignal.inFocusDisplaying(2);
            
            this.oneSignal.handleNotificationReceived().subscribe((data) => {

                this.events.publish("notification:received");
            })

            this.oneSignal.handleNotificationOpened().subscribe((data) => {

                if (!data.notification.isAppInFocus){
                    let pushData = data.notification.payload.additionalData;

                }
            });
            

            this.oneSignal.endInit();               

                       
        }
        
        
        if (platform.is("cordova") && (platform.is("android") || platform.is("ios"))){
            this.useNative = true;
        }  

        if (this.useNative){
            //use the native MQTT plugin
            mqtt.init({
                host:"b-f4be5e8b-748c-4ac6-875f-56badddbf4c7-1.mq.ap-southeast-2.amazonaws.com",
                port:8883,
                username:"wentity",
                password:'wentity@1234',
                ssl:true,
                offlineCaching:false
            });   
            
            mqtt.connect();
            
            mqtt.on("connect", () => {
                
                this.storage.get("subscription").then((data) => {
                    if (data){
                        this.mqttSubscribe(data.hubId);
                    }
                });                 
            }, (status) => {
                //alert("failed: " + status);
                this.storage.get("subscription").then((data) => {
                    if (data){   
                        /*         
                        let alert = this.alertCtrl.create({
                          title: 'Connection Failed',
                          subTitle: 'Failed to retreive Salat times from the server',
                          message: 'Please check your internet connection.',
                          buttons: ['Dismiss']
                        });
                        alert.present();
                        */
                    }
                });
            });

            
            /*
            mqtt.on("disconnect", () => {
                mqtt.connect();
            }, () => {
                mqtt.connect();
            });
            mqtt.on("reconnect", () => {
                mqtt.connect();
            }, () => {
                mqtt.connect();
            }); 
            */                    
        }   
        
        else{
            /*
             * for non android and ios
            this.client  = mqttJs.connect({
                username:"wentity",
                password:"wentity@1234",
                host:'b-f4be5e8b-748c-4ac6-875f-56badddbf4c7-1.mq.ap-southeast-2.amazonaws.com',
                port:8883,
                protocol: 'wss'
            });
            this.client.on('connect', () => {
                console.log("success");
                this.storage.get("subscription").then((data) => {
                    if (data){
                        this.mqttSubscribe(data.hubId);
                    }
                });                  
            });
            */
           
        }     
        


        this.events.subscribe('channel:subscribed', (hubId) => {

            this.mqttSubscribe(hubId);
        });            
        this.events.subscribe('channel:unsubscribed', (hubId) => {

            this.mqttUnsubscribe(hubId);
        });         

        //this.backgroundMode.setDefaults({silent:true/*, title:"Monitoring Updates", text:"Checking for updates to your Salat times in the background..."*/});
      
         
        //this.backgroundMode.enable();
        
        /*
        if( this.platform.is("android")){
            
            window.powerManagement.dim(function() {
                    console.log('Wakelock acquired');
                }, function() {
                    console.log('Failed to acquire wakelock');
                });
            window.powerManagement.setReleaseOnPause(false, function() {
                    console.log('setReleaseOnPause successfully');
                }, function() {
                    console.log('Failed to set');
                });
                

                
            this.platform.pause.subscribe(() => { 
                this.backgroundTimeout = setTimeout(() => {
                    //this.backgroundMode.moveToBackground();
                    
                    if (!this.backgroundMode.isActive()){
                        this.backgroundMode.moveToBackground();
                    }
                    
                },30000);
            });   
            this.platform.resume.subscribe(() => {
                if (this.backgroundTimeout){
                    clearTimeout(this.backgroundTimeout);
                    this.backgroundTimeout = null;
                }
            });       
                
        }  
        
        */
        
        
        /*
        if (this.platform.is("cordova")){
            cordova.plugins.notification.local.hasPermission((granted) => {
                console.log(granted);
                if (!granted){
                    cordova.plugins.notification.local.requestPermission();
                }
            })
        } 
        */     
        
        this.events.subscribe("network:online", () => {
            setTimeout(() => {
                if (this.useNative){
                    mqtt.connect();
                }
                else{
                    /*
                    this.client  = mqttJs.connect({
                        username:"wentity",
                        password:"wentity@1234",
                        host:'b-f4be5e8b-748c-4ac6-875f-56badddbf4c7-1.mq.ap-southeast-2.amazonaws.com',
                        port:8883,
                        protocol: 'wss'
                    });     
                    */           
                }
            },5000);
        })

      
    });

    platform.resume.subscribe(() => {
        setTimeout(() => {
            if (this.platform.is("android")){
                mqtt.connect();
            }
        },5000);
    });      
    
    
  }
  
    public savePushId(){
        this.oneSignal.getIds().then((data) => {

            if (data.userId){
                //save
                //this.auth.savePushId(data.userId);
                
                let user = {
                    appUserName:this.user.appUserName,
                    email: this.user.email,
                    mobile: this.user.mobile,  
                    deviceToken:data.userId                 
                };                 
                
                this.authProvider.updateUser(user);
            }
        }).catch((e) => {
        });        
    }  

  
    public mqttSubscribe(hubId){

        if (this.useNative){
            mqtt.subscribe({
                topic:hubId,
                qos:2
            }); 
            mqtt.on("subscribe", (subscription) => {

                this.mqttListen(hubId);
            }, (e) => {
            });
        }
        else if (this.client){
            this.client.subscribe(hubId, {qos: 2});
            this.mqttListen(hubId);
        }      
    }
    
    public mqttListen(hubId){
        if (this.useNative){
           mqtt.on("message", (message) => {
                
                this.events.publish("mqtt:message", {message:message});
                
                //alert("An update to your Salat times or an announcement has been made.");    
                /*
                this.localNotifications.schedule({
                  id: 1,
                  title: 'Update Received',
                  text: 'An update to your Salat times or an announcement has been made.',
                  smallIcon: 'res://small_icon'
                }); 
                */                           
           }) 
        }
        else if (this.client){
            this.client.on('message', (value) => {

                this.events.publish("mqtt:message", {message:value});
                
                alert("An update to your Salat times or an announcement has been made.");    

                                            
            });
        }     
    }

    
    public mqttUnsubscribe(hubId){
        if (this.useNative){
            mqtt.unsubscribe({
                topic:hubId
            }); 
            mqtt.on("unsubscribe", (topic) => {
 
            }, (e) => {
            });    
        } 
        else if (this.client){
            this.client.unsubscribe(hubId, {});
        } 
    }
    
    
       
    
}



