import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';
/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthenticationProvider {

  constructor(public http: HttpClient, public storage: Storage, public events:Events) {

  }
  
    public login(data){
        return new Promise((resolve, reject) => {
            this.http.post(AppSettings.apiUrl + "/users/appsignin", data).subscribe((res) => {
                    res["password"] = data["password"];
                    this.storage.set("user", res).then(() => {
                        this.events.publish("user:loggedin");  
                    });         
                    resolve(res);             
              },(e) => {
                  reject(e);
              });          
        })
    }
  
  
  
    public register(data){
        return new Promise((resolve, reject) => {
            this.http.post(AppSettings.apiUrl + "/users/appsignup", data).subscribe((res) => {
                resolve(res);
            },(e) => {
                reject(e);
            });          
        })      
    }  
  
    public resetPassword(data){
        return new Promise((resolve, reject) => {
            this.http.post(AppSettings.apiUrl + "/users/forgotpassword", data).subscribe((res) => {

                resolve(res);
            },(e) => {
                reject(e);
            });          
        })      
    }   
    
    public logout(){
        return new Promise((resolve, reject) => {
            this.storage.clear().then(() => {
                resolve();
            },(e) => {
                reject(e);
            });
        });
    }
    
    public updateUser(data){
        return new Promise((resolve, reject) => {
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);               
                    this.http.patch(AppSettings.apiUrl + "/users/secured/changeappprofile", data, { headers:headers }).subscribe((res) => {
                        resolve(res);
                    },(e) => {
                        reject(e);
                    }); 
                }
            })                             
        })      
    }     

}



