import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';

import { CountryProvider } from '../../providers/country/country';
import { ChannelProvider } from '../../providers/channel/channel';
import { Storage } from '@ionic/storage';

@Component({
    selector: 'add-channel',
    templateUrl: 'add-channel.html'
})

export class AddChannelModal {

    public cities:Array<any>;
    public regions:Array<any>;
    public channels:Array<any>;
    public subscriptions:Array<any>;
    public subscription:any;
    public region:string;
    
    
    constructor(public platform: Platform, public params: NavParams, public viewCtrl: ViewController, public countryProvider: CountryProvider, public channelProvider: ChannelProvider, private alertCtrl: AlertController, public storage: Storage) {
        
        this.subscriptions = params.data.subscriptions;
        
        this.subscription = {cityId:params.data.city, hubId:""};
        
        this.cities = [];
        this.regions = [];
        this.region = params.data.region;

        this.channels = [];  
        this.getCountries();        
        
        
        
        
    }
    
    public getCountries(){
        this.countryProvider.getCountries().then((data: Array<any>) => {
            this.regions = data[0].regionsList;
            if (this.region){
                this.getCities();
            }     
            if (this.subscription.cityId){
                this.getChannels();
            }              
                   
            
        }).catch(() => {
            
        })
    }
    
    public getCities(){
        for (let region of this.regions){
            if (parseInt(region.regionId) === parseInt(this.region)){
                this.cities = region.citiesList;
            }
        }
    }
    
    public getChannels(){
        
        this.channels = [];
        
        
        this.channelProvider.getChannels(this.subscription.cityId).then((data:Array<any>) => {
            this.channels = data;
        }).catch(() => {
            
        })
        
    }
    
    public isSubscribed(channel){
        for (let subscription of this.subscriptions){
            if (subscription.hub.hubId === channel.hubId){
                return true;
            }
        }
        return false;
    }
    
    public selectChannel(channel){
        this.viewCtrl.dismiss(channel);
    }
    
    public dismiss() {
        this.viewCtrl.dismiss();
    }
}






