import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController } from 'ionic-angular';
import { FeedbackProvider } from '../../providers/feedback/feedback';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the FeedbackPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-feedback',
  templateUrl: 'feedback.html',
})
export class FeedbackPage {
    
    public feedback:any;
    public properties:any;
    
    constructor(public navCtrl: NavController, public navParams: NavParams, public feedbackProvider: FeedbackProvider, private alertCtrl: AlertController, public storage: Storage) {
        this.feedback = {userEmail:"", message:""};
        this.properties = {loading:false, error:""}
        
        this.storage.get("user").then((data) => {
            if (data){
                this.feedback.userEmail = data.email;
            }
        })
        
          
    }

    ionViewDidLoad() {
    }
    
    public send(){
        this.properties.loading = true;
        this.feedbackProvider.sendFeedback(this.feedback).then(() => {
            this.properties.loading = false;
            let alert = this.alertCtrl.create({
              title: 'Success',
              message: 'Your post has been sent',
              buttons: [
                {
                  text: 'OK',
                  role: 'cancel'
                }
              ]
            });
            alert.present();                 
            //popup
        }).catch((e) => {
            this.properties.loading = false;

            this.properties.error = e.error;
        })
    }    

}



