import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Events, Platform} from 'ionic-angular';
import { Network } from '@ionic-native/network';

/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ConnectionProvider {
    public connectionStatus:boolean;
    public heartbeatStarted:boolean;
    public heartbeat:any;
    constructor(public http: HttpClient, public events: Events, private platform: Platform, private network: Network) {
        this.connectionStatus = (this.network.type && this.network.type !== "none") || !this.platform.is("cordova");
        
        this.platform.ready().then(() => {
            if ((!this.network.type || this.network.type === "none") && this.platform.is("cordova")){
                this.connectionStatus = false;
                this.events.publish("app:offline");
                this.startHeartbeat();
            }
        });
        
        this.network.onDisconnect().subscribe((data) => {
            this.connectionStatus = false;
            this.events.publish("network:offline");
            this.startHeartbeat();
        }, error => {
            //console.error(error)
        }); 

        this.network.onConnect().subscribe((data) => {

            this.connectionStatus = true;
            
            setTimeout(() => {
                this.events.publish("network:online");
            }, 3000);
        }, error => {
            //console.error(error)
        });      
        
                
    }
    
    public getConnectionStatus(){
        return this.connectionStatus;
    }
    
    public setConnectionStatus(status){
        
        if (this.connectionStatus !== status){       
            this.connectionStatus = status;

            if (status){
                this.events.publish("network:online");
            }
            else{
                this.events.publish("network:offline");
                this.startHeartbeat();
            }
        }
        
    }
    
    public startHeartbeat(){
        if (this.heartbeatStarted){return;}
        
        //this.events.publish("app:offline");
        this.heartbeatStarted = true;
        this.heartbeat = setInterval(() => {
            this.http.get(AppSettings.apiUrl + "/countries").subscribe((res) => {
                this.connectionStatus = true;
                this.events.publish("network:online");
                clearInterval(this.heartbeat);
                this.heartbeatStarted = false;
            });
            
        },20000);         
    }

     

}



