import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { ConnectionProvider } from '../../providers/connection/connection';
/*
  Generated class for the ChannelProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DirectoryProvider {

    constructor(public http: HttpClient, public storage: Storage, public connectionProvider:ConnectionProvider) {

    }
  
    public getRegions(){
        return new Promise((resolve, reject) => {
            
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.get(AppSettings.apiUrl + "/regions", { headers:headers }).subscribe((res) => {
                            this.connectionProvider.setConnectionStatus(true);
                            resolve(res);             
                      },(e) => {
                            if (e && e.status === 0){
                                this.connectionProvider.setConnectionStatus(false);
                            }
                            reject(e);
                      });  
                } 
              })       
        })
    }    
    public getDirectoryTypes(){
        return new Promise((resolve, reject) => {
            
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.get(AppSettings.apiUrl + "/directorytypes", { headers:headers }).subscribe((res) => {
                            this.connectionProvider.setConnectionStatus(true);
                            resolve(res);             
                      },(e) => {
                            if (e && e.status === 0){
                                this.connectionProvider.setConnectionStatus(false);
                            }
                            reject(e);
                      });  
                } 
              })       
        })
    }     
    
    public getDirectory(cityId, directoryTypeId){
        return new Promise((resolve, reject) => {
            
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.get(AppSettings.apiUrl + "/halaldirectories/findBy/" + directoryTypeId + "/" + cityId, { headers:headers }).subscribe((res) => {
                            this.connectionProvider.setConnectionStatus(true);
                            resolve(res);             
                      },(e) => {
                            if (e && e.status === 0){
                                this.connectionProvider.setConnectionStatus(false);
                            }
                            reject(e);
                      });  
                } 
              })       
        })
    }          
}



