import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { Platform } from 'ionic-angular';
declare var cordova: any;
import * as moment from 'moment';
/*
  Generated class for the FeedbackProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SchedulerProvider {
    
    public settings:any;
    public properties:any;

    constructor(public storage: Storage, public platform: Platform, private localNotifications: LocalNotifications) {

      this.properties = {isSettingNotifcations:false};
      this.settings = {sound:"sound"};
        this.storage.get("settings").then((data) => {
            if (data){
                this.settings = data;
            }
        }); 
             
    }
    
    
    public generateNotifications(prayerTimes){

        
        
        return new Promise((resolve, reject) => {
            
            if (this.properties.isSettingNotifications){
                resolve();return;
            }

            if (!this.platform.is("cordova")){
                resolve();return;
            }
            
            
            this.properties.isSettingNotifications = true;
            
            
            
            this.localNotifications.getScheduledIds().then((ids) => {
                let notPrayerTime = ids.indexOf(1);
                if (notPrayerTime > -1){
                    ids.splice(notPrayerTime,1);
                }

                notPrayerTime = ids.indexOf(2);
                if (notPrayerTime > -1){
                    ids.splice(notPrayerTime,1);
                }                      
                
                let timeoutTime = ids.length * 50;
                if (timeoutTime < 5000){
                    timeoutTime = 5000;
                }
                else if (timeoutTime > 15000){
                    timeoutTime = 15000;
                }
                
                this.localNotifications.cancel(ids).then(() => {
                    this.scheduleNotifications(prayerTimes, timeoutTime);
                    resolve();
                }).catch(() => {
                    this.scheduleNotifications(prayerTimes, timeoutTime);
                    resolve();
                });



            }).catch(() => {
                this.scheduleNotifications(prayerTimes, 5000);
                resolve();
            }); 
           
            
            
            /*
            this.localNotifications.cancelAll().then(() => {
                this.scheduleNotifications(prayerTimes);
                resolve();                
            }).catch(() => {
                this.scheduleNotifications(prayerTimes);
                resolve();                
            })  
            */        
            
            
            
            
             
        })         

    }

    
    private scheduleNotifications(prayerTimes, timeoutTime){
        
            setTimeout(() => {       
                let id = 3;

                for (let prayer of prayerTimes){
                    let prayerTime = moment((prayer.activateDateTime + " " + prayer.adzanTime), 'DD-MM-YYYY hh:mm A');
                    let prayerTime2 = moment((prayer.activateDateTime + " " + prayer.ikhamaTime), 'DD-MM-YYYY hh:mm A');

                    if (prayer.autoTimeUp && prayer.autoTimeUp > 0){
                        prayerTime.add(prayer.autoTimeUp, 'minutes');
                        prayerTime2.add(prayer.autoTimeUp, 'minutes');
                    }

                    let prayerExpiry = moment((prayer.expireDateTime + " " + prayer.adzanTime), 'DD-MM-YYYY hh:mm A');
                    let days = prayerExpiry.diff(prayerTime, 'days') + 1;
                    if (days > 7){
                        days = 7;
                    }
                    else if (days < 2){
                        days = 2;
                    }
                    let today = moment().set({hour:0,minute:0,second:0,millisecond:0});;

                    let sound = "";
                    
                    //set sounds
                    if (prayer.prayerName === "Fajar" && (this.settings && this.settings.sound !== "default")){
                        if (this.platform.is('android')) {
                            sound = 'file://assets/audio/Azan-S.mp3';
                        } else {
                            sound = 'file://assets/audio/Azan-IOS.caf';
                        }                        
                    }
                    else if (prayer.prayerName === "Day" && (this.settings && this.settings.sound !== "default")){
                        sound = '';
                    }                    
                    else if (prayer.prayerName !== "Tahajjud" && prayer.prayerName !== "Chasht" && (this.settings && this.settings.sound !== "default")){
                        sound = 'file://assets/audio/Azan-A.mp3';
                        if (this.platform.is('android')) {
                            sound = 'file://assets/audio/Azan-A.mp3';
                        } else {
                            sound = 'file://assets/audio/Azan-IOS.caf';
                        }                         
                    }     
                    
                    let prayerType1 = "Adzan";
                    let prayerType2 = "Ikhama";  
                                      
                    //set prayer names 
          
                    if (prayer.prayerName === "Day"){
                        prayerType1 = "Sunrise";
                        prayerType2 = "Sunset";                        
                    }   
                    else if (prayer.prayerName === "Tahajjud"){
                        prayerType1 = "Start";
                        prayerType2 = "End";                          
                    }

                    let channel = ((prayer && prayer.prayerName) ?  prayer.prayerName.toLowerCase().split(' ').join('') : "prayer");


                    for (let i = 0; i < days; i++){
                        
                        let prayerTimeYear = prayerTime.year();
                        let prayerTimeMonth = prayerTime.month();
                        let prayerTimeDay = prayerTime.date();
                        let prayerTimeHour = prayerTime.hour();
                        let prayerTimeMinutes = prayerTime.minutes();
                        
                        let prayerTimeDayOfWeek = prayerTime.isoWeekday();
                        let shouldSchedule = prayer.prayerName !== "Day" && ((prayerTimeDayOfWeek === 5 && prayer.fridayPrayer) || (prayerTimeDayOfWeek !== 5 && !prayer.fridayOnly));
                        
                        if (prayerTime.isAfter(today) && shouldSchedule){
                            cordova.plugins.notification.local.schedule({
                                id:id,
                                title: prayer.prayerName + " prayer (" + prayerType1 + ")",
                                text: "Starts at " + prayer.adzanTime.trim(),
                                smallIcon: 'res://small_icon',
                                sound: sound,
                                foreground: true,
                                vibrate:true,
                                channel:channel + "adzan",
                                trigger: {at: new Date(prayerTimeYear, prayerTimeMonth, prayerTimeDay, prayerTimeHour, prayerTimeMinutes)}
                            }); 

                            id++
                        }
                        
                        let prayerTime2Year = prayerTime2.year();
                        let prayerTime2Month = prayerTime2.month();
                        let prayerTime2Day = prayerTime2.date();
                        let prayerTime2Hour = prayerTime2.hour();
                        let prayerTime2Minutes = prayerTime2.minutes();

                        let prayerTime2DayOfWeek = prayerTime2.isoWeekday();
                        let shouldSchedule2 = prayer.prayerName !== "Day" && ((prayerTime2DayOfWeek === 5 && prayer.fridayPrayer) || (prayerTimeDayOfWeek !== 5 && !prayer.fridayOnly));

                        if (prayerTime2.isAfter(today) && shouldSchedule2){
                            cordova.plugins.notification.local.schedule({
                                id:id,
                                title: prayer.prayerName + " prayer (" + prayerType2 + ")",
                                text: "Starts at " + prayer.ikhamaTime.trim(),
                                smallIcon: 'res://small_icon',
                                sound: '',
                                foreground: true,
                                vibrate:true,
                                channel:"ikhama",
                                trigger: {at: new Date(prayerTime2Year, prayerTime2Month, prayerTime2Day, prayerTime2Hour, prayerTime2Minutes)}
                            }); 
                            id++
                        }     


                        prayerTime.add(1,'days');    
                        prayerTime2.add(1,'days');     
                    }


                }
                this.properties.isSettingNotifications = false;    
            },timeoutTime);    
    }    
    
    
    private dailyNotifications(){
        cordova.plugins.notification.local.schedule({
          id: 2,
          title: 'Check your Salat times',
          text: 'Open the app to ensure you have the most up to date Salat times',
          smallIcon: 'res://small_icon',
          channel:"channel1",
          vibrate:true,
          trigger:{every: { hour: 17, minute: 0 }}
        });          
    }
    
    

}



