import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { ConnectionProvider } from '../../providers/connection/connection';
/*
  Generated class for the CountryProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CountryProvider {

  constructor(public http: HttpClient, public connectionProvider:ConnectionProvider) {

  }
  
    public getCountries(){
        return new Promise((resolve, reject) => {
            this.http.get(AppSettings.apiUrl + "/countries").subscribe((res) => {   
                this.connectionProvider.setConnectionStatus(true);     
                    resolve(res);             
              },(e) => {
                if (e && e.status === 0){
                    this.connectionProvider.setConnectionStatus(false);
                }
                reject(e);
              });          
        })
    }  
  

}



