import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { SignUpPage } from '../sign-up/sign-up';
import { ForgotPasswordPage } from '../forgot-password/forgot-password';
import { TabsPage } from '../tabs/tabs';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { OneSignal } from '@ionic-native/onesignal';
/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

    public user:any;
    public properties:any;
    
    constructor(public navCtrl: NavController, public navParams: NavParams, public authProvider: AuthenticationProvider, public oneSignal:OneSignal) {
        this.user = {appUserName: "", email:"", password:"", mobile: "", deviceToken:""};
        this.properties = {loading:false, error:""};
        
        this.savePushId();

    }
    
    
    public savePushId(){
        this.oneSignal.getIds().then((data) => {

            if (data.userId){
                this.user.deviceToken = data.userId;
            }
        }).catch((e) => {
            //console.log(e);
        });        
    }     
    

  
    public login(){
        this.properties.loading = true;
        this.properties.error = "";
        this.authProvider.login(this.user).then((user) => {
            this.properties.loading = false;
            this.navCtrl.setRoot(TabsPage);
            this.navCtrl.popToRoot();
        }).catch((e) => {
            this.properties.loading = false;
            this.properties.error = e.error ? e.error.message : JSON.stringify(e);
 
        })
    }
    
    public signup(){
        this.navCtrl.push(SignUpPage);
    }

    public forgotPassword(){
        this.navCtrl.push(ForgotPasswordPage);
    }

}



