import { Component} from '@angular/core';

import { PrayerPage } from '../prayer/prayer';
import { EventsPage } from '../events/events';
import { ChannelsPage } from '../channels/channels';
import { SettingsPage } from '../settings/settings';
import { FeedbackPage } from '../feedback/feedback';
import { DirectoryPage } from '../directory/directory';
import { LoginPage } from '../login/login';


import { AlertController, NavController, App,Events, Platform } from 'ionic-angular';

import { AuthenticationProvider } from '../../providers/authentication/authentication';


@Component({
    templateUrl: 'tabs.html'
})
export class TabsPage {
    
    tab1Root = SettingsPage;  
    tab2Root = EventsPage;
    tab3Root = PrayerPage;
    tab4Root = DirectoryPage;
    tab5Root = ChannelsPage;
    

    constructor(public app: App, private alertCtrl: AlertController, private platform:Platform, public authProvider: AuthenticationProvider, private events:Events) {
  
        
    }

    public logout(){
        let alert = this.alertCtrl.create({
          title: 'Logout',
          message: 'Are you sure you want to logout?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel'
            },
            {
              text: 'Yes',
              handler: () => {
                    //logout
                  this.authProvider.logout().then(() => {
                    this.app.getRootNav().setRoot(LoginPage);
                    this.app.getRootNav().popToRoot();                        
                  });
              }
            }
          ]
        });
        alert.present();        
    }
  
}



