import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { ConnectionProvider } from '../../providers/connection/connection';
/*
  Generated class for the ChannelProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ChannelProvider {

    constructor(public http: HttpClient, public storage: Storage, public connectionProvider:ConnectionProvider) {

    }
  
    public getChannels(cityId){
        return new Promise((resolve, reject) => {
            
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.get(AppSettings.apiUrl + "/secured/channel/listbycity/" + cityId, { headers:headers }).subscribe((res) => {
                            this.connectionProvider.setConnectionStatus(true);
                            resolve(res);             
                      },(e) => {
                            if (e && e.status === 0){
                                this.connectionProvider.setConnectionStatus(false);
                            }
                            reject(e);
                      });  
                } 
              })       
        })
    }    

  
    public subscribe(userEmail, hubId){
        return new Promise((resolve, reject) => {
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.post(AppSettings.apiUrl + "/secured/subscribers/subscribe", {userEmail:userEmail, hub:hubId}, { headers:headers }).subscribe((res) => {   
    
                            resolve(res);             
                      },(e) => {
                          reject(e);
                      });  
                } 
              });        
        })
    }  
    
    public unsubscribe(subscriberId){
        return new Promise((resolve, reject) => {
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.delete(AppSettings.apiUrl + "/secured/subscribers/unsubscribe/" + subscriberId, { headers:headers }).subscribe((res) => {           
                            resolve(res);             
                      },(e) => {
                          reject(e);
                      });  
                } 
              });               
              
              
                    
        })
    } 
    
    public getSubscriptions(userEmail){
        return new Promise((resolve, reject) => {
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.get(AppSettings.apiUrl + "/secured/subscribers/subscrictionlist/" + userEmail, { headers:headers }).subscribe((res) => {   
   
                            resolve(res);             
                      },(e) => {
                          reject(e);
                      });  
                } 
              });        
        })
    }      
    
    
    
    public getChannel(subscriberId){
        return new Promise((resolve, reject) => {
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.get(AppSettings.apiUrl + "/secured/subscribers/channelview/" + subscriberId, { headers:headers }).subscribe((res) => {        
                            resolve(res);  
                            this.connectionProvider.setConnectionStatus(true);                                       
                      },(e) => {
                            if (e && e.status === 0){
                                this.connectionProvider.setConnectionStatus(false);
                            }                      
                          reject(e);
                      });  
                } 
              })                
              
              
              
                      
        })
    }         
    
    public getSalatTimes(hubId){
        return new Promise((resolve, reject) => {
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.get(AppSettings.apiUrl + "/secured/salattimes/listby/" + hubId + "/1/99", { headers:headers }).subscribe((res) => {        
                            this.connectionProvider.setConnectionStatus(true);
                            resolve(res);             
                      },(e) => {
                            if (e && e.status === 0){
                                this.connectionProvider.setConnectionStatus(false);
                            }                      
                          reject(e);
                      });  
                } 
              })                   
                      
        })
    } 
    
    public getAnnouncements(hubId){
        return new Promise((resolve, reject) => {
            this.storage.get("user").then((user) => {
                if (user){
                    let token = btoa(user["email"] + ":" + user["password"]);
                    const headers = new HttpHeaders().set('Authorization', token);         

                    this.http.get(AppSettings.apiUrl + "/secured/events/listby/" + hubId + "/1/99", { headers:headers }).subscribe((res) => {      
                        this.connectionProvider.setConnectionStatus(true);  
                            resolve(res);             
                      },(e) => {
                            if (e && e.status === 0){
                                this.connectionProvider.setConnectionStatus(false);
                            }                       
                          reject(e);
                      });  
                } 
              })                   
                      
        })
    }    
    
         
}



