import { Component,NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { AnnouncementModal } from '../../modals/announcement/announcement';
import { ChannelProvider } from '../../providers/channel/channel';

import * as moment from 'moment';

import { LocalNotifications } from '@ionic-native/local-notifications';

/**
 * Generated class for the EventsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-events',
  templateUrl: 'events.html',
})
export class EventsPage {
    
    
    public loading:boolean;
    public channelLoading:boolean;
    public subscribedChannel:any;   
    public announcements: any; 
    public subscriptions:Array<any>;

    constructor(public navCtrl: NavController, public navParams: NavParams,public storage: Storage, private localNotifications: LocalNotifications, public modalCtrl: ModalController, public events : Events, public channelProvider: ChannelProvider, private ngZone: NgZone) {
        this.announcements = {};
        this.loading = true;
        this.channelLoading = true;
        
        this.events.subscribe("mqtt:message", (message) => {
            this.getAnnouncements();            
        });   
        
        this.events.subscribe("network:online", () => {
            this.getAnnouncements();
        });  
        
        this.events.subscribe("notification:received", () => {
            this.getAnnouncements();
        });  
        
       
                      
        
             
    }

  
    ionViewWillEnter(){
        this.channelLoading = true;
        this.loading = true;
        this.subscribedChannel = null;
  
        this.subscriptions = [];
        this.storage.get("subscriptions").then((data) => {
           if (data){
               this.channelLoading = false;
               this.subscriptions = data;
               this.getAnnouncements();
               this.openSubscribedChannel();
               
           }  
           else{
               this.loading = false;
               this.subscriptions = [];
           }          
       }); 
       
       
        this.storage.get("subscribedChannel").then((data) => {

            
            if (data){
                this.subscribedChannel = data;
                this.openSubscribedChannel();
            }
            else{
                
            }
        });        
       
       
                     
    }
    
    public openSubscribedChannel(){
        if (!this.subscribedChannel){
            return;
        }
        
        for (var index in this.subscriptions){
            if (this.subscriptions[index]["hub"]["hubId"] === this.subscribedChannel.hubId){
                this.ngZone.run(() => {
                    this.subscriptions[index].opened = true;
                });
            }
        }
    }    


    
    public openChannel(item){
        item.opened = !item.opened;    
    }     
        
    public getAnnouncements(){
        
        for (let subscription of this.subscriptions){    

            this.channelProvider.getAnnouncements(subscription.hub.hubId).then((announcements:Array<any>) => {
                this.loading = false;
                this.ngZone.run(() => {  
                    this.announcements[subscription.hub.hubId] = this.filterAnnouncements(announcements);
                });
                this.storage.set("announcements", this.announcements);
                

            }).catch(() => {
                this.loading = false;
                this.storage.get("announcements").then((data) => {
                    if (data){
                        this.ngZone.run(() => {
                            this.announcements = this.filterAnnouncements(data);
                        });
                    }
                });                 
            });    
        } 
       
    }  
    
    private filterAnnouncements(announcements){
        let filteredAnnouncements = [];

        for (let announcement of announcements){
            let announcementDateEnd = moment(announcement.eventEnd, 'DD-MM-YYYY hh:mm A');
            let announcementDateStart = moment(announcement.eventStart, 'DD-MM-YYYY hh:mm A');
            let today = moment();

            if (announcementDateEnd.isAfter(today) /* && announcementDateStart.isBefore(today)*/){
                filteredAnnouncements.push(announcement);
            }
        }
        
        return filteredAnnouncements;        
    }      
    
    public openAnnouncement(announcement){
        let modal = this.modalCtrl.create(AnnouncementModal, {announcement:announcement}); 
        modal.present();          
    }
    
    public stripHtml(html:string){
        return html.replace(/<(?:.|\n)*?>/gm, '');
    }
    
    public formatDate(date){
        if (date){
            return moment(date).format("DD-MM-YYYY hh:mm A");
        }
        return "";
    }

}



