import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController } from 'ionic-angular';
import { DirectoryProvider } from '../../providers/directory/directory';
import { Storage } from '@ionic/storage';
import { InAppBrowser } from '@ionic-native/in-app-browser';

/**
 * Generated class for the FeedbackPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-directory',
  templateUrl: 'directory.html',
})
export class DirectoryPage {

    public properties:any;
    public regions:Array<any>;
    public cities:Array<any>;
    public directoryTypes:Array<any>;
    public directory:Array<any>;
    
    constructor(public navCtrl: NavController, public navParams: NavParams, public directoryProvider: DirectoryProvider, private alertCtrl: AlertController, public storage: Storage, private iab: InAppBrowser) {

        this.properties = {loading:false, error:"", regionId:"", cityId:"", directoryTypeId:""};
        
        this.regions = [];
        this.cities = [];
        this.directoryTypes = [];
        this.directory = [];
        
        this.directoryProvider.getRegions().then((data:Array<any>) => {

            this.regions = data;
        });
        
        this.directoryProvider.getDirectoryTypes().then((data:Array<any>) => {

            this.directoryTypes = data;
        })
        
        this.storage.get("user").then((data) => {
            if (data){

            }
        })
        
          
    }
    
    public setCityList(regionId){
        for (let region of this.regions){
            if (region.regionId === regionId){
                this.cities = region.citiesList;
            }
        }
    }
    
    public updateDirectory(){
        if (this.properties.cityId && this.properties.directoryTypeId){
            this.directoryProvider.getDirectory(this.properties.cityId, this.properties.directoryTypeId).then((data:Array<any>) => {

                if (data["message"]){
                    this.directory = [];
                    return;
                }
                this.directory = data.sort((a,b) => {
                    if (a.featured && !b.featured){
                        return -1;
                    }
                    
                    if (!a.featured && b.featured){
                        return 1
                    }
                    
                    return 0;
                });
            })
        }
    }
    
    public openItem(item){
        let prefix = 'http://';
        let prefix2 = 'https://';
        let url = item.web;
        if (url.substr(0, prefix.length) !== prefix && url.substr(0, prefix2.length) !== prefix2)
        {
            url = prefix + url;
        }   
        window.open(url, '_system');

            
    }

    ionViewDidLoad() {
    }


}



