import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { TabsPage } from '../tabs/tabs';
import { TermsConditionsModal } from '../../modals/terms-conditions/terms-conditions';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { OneSignal } from '@ionic-native/onesignal';

/**
 * Generated class for the SignUpPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-sign-up',
  templateUrl: 'sign-up.html',
})
export class SignUpPage {
    
    public user:any;
    public properties:any;
    
    constructor(public navCtrl: NavController, public navParams: NavParams, public authProvider: AuthenticationProvider, public modalCtrl: ModalController, public oneSignal:OneSignal) {
        this.user = {appUserName: "", email:"", password:"", verifyPassword:"", mobile: "", deviceToken:""};
        this.properties = {loading:false, error:""}      

    }
    
    public savePushId(){
        this.oneSignal.getIds().then((data) => {

            if (data.userId){
                this.user.deviceToken = data.userId;
            }
        }).catch((e) => {
            //console.log(e);
        });        
    }       
    
    public signup(){
        this.properties.loading = true;
        this.properties.error = "";
        
        if (this.user.password !== this.user.verifyPassword){
            this.properties.loading = false;
            this.properties.error = "Password do not match";
            return;
        }
        delete this.user.verifyPassword;
        this.authProvider.register(this.user).then((data) => {
            
            this.doLogin();

        }).catch((e) => {
            this.properties.loading = false;

            if (e && e.status === 201){
                this.doLogin();
            }
            this.properties.error = "There was an error";
        })
    }
    
    public doLogin(){
        this.authProvider.login(this.user).then((user) => {
            this.properties.loading = false;
            this.navCtrl.setRoot(TabsPage);
            this.navCtrl.popToRoot();                
        }).catch((e) => {
            this.properties.loading = false;
            this.properties.error = "Could not login";
        })        
    }
    
    public login(){
        this.navCtrl.push(LoginPage);
    }  
    
    public openTos(){
        let modal = this.modalCtrl.create(TermsConditionsModal); 
        modal.present();        
    }  
    
    

  ionViewDidLoad() {
  }

}



