import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController, Events,Platform } from 'ionic-angular';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { Storage } from '@ionic/storage';
import { LocalNotifications } from '@ionic-native/local-notifications';
declare var cordova: any;

/**
 * Generated class for the SettingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {
    
    public user:any;
    public password:any;
    public properties:any;
    public settings:any;

    constructor(public navCtrl: NavController, public navParams: NavParams, public events: Events, public authProvider: AuthenticationProvider, public storage: Storage, private alertCtrl: AlertController, private localNotifications: LocalNotifications, public platform: Platform) {

        this.user = {appUserName: "", email:"", mobile:"", password:""};
        this.settings = {sound:"sound"};
        
        
        this.storage.get("settings").then((data) => {
            if (data){
                this.settings = data;
            }
        })        
        
        this.properties = {loading:false, error:"", confirm_password:""}  
        
        this.storage.get("user").then((data) => {
            if (data){
                //this.user.userId = data.userId;
                this.user.appUserName = data.appUserName;
                this.user.email = data.email;
                this.user.mobile = data.mobile;
                this.user.password = "";          
            }  
        });

    }



    ionViewDidLoad() {
    }
    
    
    public save(){
        this.properties.loading = true;
        let user = JSON.parse(JSON.stringify(this.user));
        if (!user.password){delete user.password;}
        else{
            if (user.password !== this.properties.confirm_password){
                let alert = this.alertCtrl.create({
                  title: 'Error',
                  message: 'Passwords do not match.',
                  buttons: [
                    {
                      text: 'OK',
                      role: 'cancel'
                    }
                  ]
                });
                alert.present();    
                return;             
            }
        }
        this.storage.set("settings", this.settings);
        this.events.publish("settings:updated", this.settings);
        this.authProvider.updateUser(user).then((data) => {
            this.properties.loading = false;
            this.user.password = "";
            this.properties.confirm_password = "";
            this.storage.get("user").then((sUser) => {
                if (sUser){
                    sUser.appUserName = user.appUserName;
                    sUser.email = user.email;
                    sUser.mobile = user.mobile;    
                    this.storage.set("user", sUser);                
                }
            })
            let alert = this.alertCtrl.create({
              title: 'Success',
              message: 'Your user details have been updated',
              buttons: [
                {
                  text: 'OK',
                  role: 'cancel'
                }
              ]
            });
            alert.present();                 
            //popup
        }).catch((e) => {
            this.properties.loading = false;

            this.properties.error = e.error.message;
        })
    }
    
    

    
       
    public testNotificationsNoChannel(){
        let notification = {
            id:1,
            title: "Test notification",
            text: "This is a test",
            smallIcon: 'res://small_icon',
            foreground: true,
            vibrate:true,
            sound: 'file://assets/audio/Azan-S.mp3'
        };

        cordova.plugins.notification.local.schedule(notification);         
    }
    
    public testNotifications(notificationType){
        let sound = "";
        let channel = "channel1";
        if (notificationType === "Azan A"){           
            if (this.platform.is('android')) {
                sound = 'file://assets/audio/Azan-A.mp3';
            } else {
                sound = 'file://assets/audio/Azan-IOS.caf';
            }              
            channel = "channel2";
        }
        else if (notificationType === "Azan S"){
            if (this.platform.is('android')) {
                sound = 'file://assets/audio/Azan-S.mp3';
            } else {
                sound = 'file://assets/audio/Azan-IOS.caf';
            } 
            channel = "channel3";
        }
        
        let notification = {
            id:1,
            title: "Test notification",
            text: "This is a test",
            smallIcon: 'res://small_icon',
            foreground: true,
            channel:channel,
            vibrate:true,
            sound: sound
        };
        
        
        this.localNotifications.schedule(notification);
        
        
        //cordova.plugins.notification.local.schedule(notification);             
    }

}



