import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { LoginPage } from '../login/login';

/**
 * Generated class for the ForgotPasswordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-forgot-password',
  templateUrl: 'forgot-password.html',
})
export class ForgotPasswordPage {
    public user:any;
    public properties:any;
    constructor(public navCtrl: NavController, public navParams: NavParams, public authProvider: AuthenticationProvider) {
        
        this.user = {email:""};
        this.properties = {loading:false, error:""}          
        
    }
    
    public signup(){
        this.properties.loading = true;
        this.properties.error = "";

        this.authProvider.resetPassword(this.user).then((token) => {
            this.properties.loading = false;
            this.navCtrl.setRoot(LoginPage);
            this.navCtrl.popToRoot();
        }).catch((e) => {
            this.properties.loading = false;

            this.properties.error = e.error;
        })
    }
    
    public login(){
        this.navCtrl.push(LoginPage);
    }     
    
    

    ionViewDidLoad() {
    }

}



