import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';


@Component({
    selector: 'announcement',
    templateUrl: 'announcement.html'
})

export class AnnouncementModal {

    public announcement:any;
    constructor(public platform: Platform, public params: NavParams, public viewCtrl: ViewController, private iab: InAppBrowser) {
        
        this.announcement = params.data.announcement;
    }
    
    public openAttachment(url){
        this.iab.create(url,"_system");
    }
    

    public dismiss() {
        this.viewCtrl.dismiss();
    }
}






