import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { HttpClientModule } from '@angular/common/http';
import { IonicStorageModule } from '@ionic/storage';
import { MyApp } from './app.component';

import { LoginPage } from '../pages/login/login';
import { SignUpPage } from '../pages/sign-up/sign-up';
import { ForgotPasswordPage } from '../pages/forgot-password/forgot-password';


import { PrayerPage } from '../pages/prayer/prayer';
import { ChannelsPage } from '../pages/channels/channels';
import { EventsPage } from '../pages/events/events';
import { SettingsPage } from '../pages/settings/settings';
import { FeedbackPage } from '../pages/feedback/feedback';
import { DirectoryPage } from '../pages/directory/directory';

import { TabsPage } from '../pages/tabs/tabs';

import { TermsConditionsModal } from '../modals/terms-conditions/terms-conditions';
import { AnnouncementModal } from '../modals/announcement/announcement';
import { AddChannelModal } from '../modals/add-channel/add-channel';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { AuthenticationProvider } from '../providers/authentication/authentication';
import { CountryProvider } from '../providers/country/country';
import { ChannelProvider } from '../providers/channel/channel';
import { FeedbackProvider } from '../providers/feedback/feedback';
import { ConnectionProvider } from '../providers/connection/connection';
import { SchedulerProvider } from '../providers/scheduler/scheduler';
import { DirectoryProvider } from '../providers/directory/directory';

import { Geolocation } from '@ionic-native/geolocation';
import { NativeGeocoder} from '@ionic-native/native-geocoder';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { BackgroundMode } from '@ionic-native/background-mode';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Network } from '@ionic-native/network';
import { OneSignal } from '@ionic-native/onesignal';

@NgModule({
  declarations: [
    MyApp,
    PrayerPage,
    EventsPage,
    SettingsPage,
    FeedbackPage,
    ChannelsPage,
    LoginPage,
    SignUpPage,
    ForgotPasswordPage,
    TabsPage,
    DirectoryPage,
    TermsConditionsModal,
    AnnouncementModal,
    AddChannelModal
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot(),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    PrayerPage,
    EventsPage,
    SettingsPage,
    FeedbackPage, 
    ChannelsPage,   
    LoginPage,
    SignUpPage,
    ForgotPasswordPage,    
    TabsPage,
    DirectoryPage,
    TermsConditionsModal,
    AnnouncementModal,
    AddChannelModal
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthenticationProvider,
    CountryProvider,
    ChannelProvider,
    FeedbackProvider,
    ConnectionProvider,
    SchedulerProvider,
    DirectoryProvider,
    Geolocation,
    NativeGeocoder,
    LocalNotifications,
    BackgroundMode,
    InAppBrowser,
    Network,
    OneSignal
  ]
})
export class AppModule {}



